* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont,
   "Segoe UI",
   "Roboto",
   "Oxygen",
   "Ubuntu",
   "Cantarell",
   "Fira Sans",
   "Droid Sans",
   "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #262626;
  color: rgb(170, 168, 168);
}
.card .card-action a:not(.btn):not(.btn-large):not(.btn-small):not(.btn-large):not(.btn-floating) {
  line-height: 36px;
}
.main-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0,0,0, .9);
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),
              0 3px 1px -2px rgba(0,0,0,0.12),
              0 1px 5px 0 rgba(0,0,0,0.2);
  z-index: 10;
}
.brand-logo, nav ul a {
  color: #ffab40;
}
.main-header > nav.container {
  background-color: transparent; /* #ee6e73; mtrlz-default-style */
  height: 64px;
  line-height: 64px;
  box-shadow: initial; /* I changed mtrlz-default-style */
}

/*==== Hero ===================*/
.hero {
  position: relative;
  width: 100%;
  height: 100vh;
  background: rgba(0,0,0, .8);
  overflow: hidden;
}
.bg-video {
  position: absolute;
  top: 0;
  left: 0;
  right:0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -10;
}
.hero > h1, .hero > h4, .hero > a {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.hero > h1 {
  top: 20%;
  text-shadow: 0 0 15px rgba(255,255,255, .3);
}
.hero > h4 {
  top: 35%;
  text-shadow: 0 0 15px rgba(255,255,255, .3);
  color: #ffab40;
}
.hero > a {
  top: 80%;
}
.material-icons {
  font-size: 32px;
  color: #ffab40;
}
/*======== about ====================*/
.section-heading {
  font-size: 32px;
  margin: initial;
  text-align: center;
  padding: 70px 0;
}
.margin-bottom {
  margin-bottom: 20px;
}
article > .about-me, article > .about-skills {
  background-color: #333;
  height: 400px;
  border-radius: 2px;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),
              0 3px 1px -2px rgba(0,0,0,0.12),
              0 1px 5px 0 rgba(0,0,0,0.2);
}
article > .about-me {
  padding: 30px 0 0 0;
}
.me-wrapper {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 5px solid transparent;
  box-shadow: 0 0 5px rgba(0,0,0, .8);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
img.me {
  width: 100px;
  height: auto;  
}
article > .about-me > h1 {
  font-size: 16px;
  text-align: center;
}
article > .about-me > p {
  font-size: 14px;
  text-align: center;
  width: 80%;
  margin: 0 auto;
}
/*======== Portfolio =================*/
.portfolio > .container > .row {
  margin-bottom: 0;
  padding-bottom: 20px;
}
.portfolio {
  background-color: #1d1d1d;
}
.card .card-action {
  background-color: inherit;
  border-top: 1px solid rgba(160,160,160,0.2);
  position: relative;
  padding: 16px 24px;
  display: flex;
  justify-content: space-between;
}
/*========== Contact ================*/
.contact {
  background-color: #131313;
}
.contact > .container > .row {
  margin-bottom:0;
  padding-bottom: 20px;
}
.card {
  background-color: #333;
}
.card-title {
  display: flex;
  align-items: center;
}
.margin-right {
  margin-right: 10px;
}
/*=========== Footer ===============*/
footer {
  background: #0f0f0f;
}
.footer-p {
  text-align: center;
  padding: 20px 0;
  margin: 0;
}